<template>
  <div class="register-container">
    <!-- 注册内容 -->
    <div class="register">
      <h3>注册新用户
        <span class="go">我有账号，去 <a href="login.html"
             target="_blank">登录</a>
        </span>
      </h3>
      <div class="content">
        <label>手机号:</label>
        <input type="text"
               placeholder="请输入你的手机号"
               v-model="phone">
        <span class="error-msg"
              v-show="phoneError!=''">{{phoneError}}</span>
      </div>
      <div class="content">
        <label>验证码:</label>
        <input type="text"
               placeholder="请输入验证码"
               v-model="code">
        <button style="width:90px;height:38px"
                @click="getCode">获取验证码</button>
        <span class="error-msg">错误提示信息</span>
      </div>
      <div class="content">
        <label>登录密码:</label>
        <input type="password"
               placeholder="请输入你的登录密码"
               v-model="password">
        <span class="error-msg">错误提示信息</span>
      </div>
      <div class="content">
        <label>确认密码:</label>
        <input type="password"
               placeholder="请输入确认密码"
               v-model="password2">
        <span class="error-msg"
              v-show="password2Error!=''">{{password2Error}}</span>
      </div>
      <div class="controls">
        <input name="m1"
               type="checkbox"
               v-model="agree">
        <span>同意协议并注册《尚品汇用户协议》</span>
        <span class="error-msg">错误提示信息</span>
      </div>
      <div class="btn">
        <button @click="userRegister">完成注册</button>
      </div>
    </div>

    <!-- 底部 -->
    <div class="copyright">
      <ul>
        <li>关于我们</li>
        <li>联系我们</li>
        <li>联系客服</li>
        <li>商家入驻</li>
        <li>营销中心</li>
        <li>手机尚品汇</li>
        <li>销售联盟</li>
        <li>尚品汇社区</li>
      </ul>
      <div class="address">地址：北京市昌平区宏福科技园综合楼6层</div>
      <div class="beian">京ICP备19006430号
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RouterRegister',
  data () {
    return {
      // 收集表单数据，手机号，验证码，密码，确认密码，是否统一协议
      phone: '',
      code: '',
      password: '',
      password2: '',
      agree: false,
    }
  },

  methods: {
    // 获取验证码
    async getCode () {
      try {
        await this.$store.dispatch('getCode', this.phone);
        this.code = this.$store.state.user.testCodeFromServer;
      }
      catch (error) {
        // 3、失败-- 提示用户失败
        alert(error.message);
      }
    },

    // 用户注册
    async userRegister () {
      if (this.phoneError != '' || this.password2Error != '' || this.agree == false) {
        alert('请先完成表单输入');
        return;
      }
      try {
        // 此处传往后台的属性名称关键字要按照后台记录的写，因为后台需要对data这个对象进行检验，需要名称一致
        const { phone, code, password } = this;
        await this.$store.dispatch("userRegister", { phone, code, password });
        //注册成功进行路由的跳转
        this.$router.push("/login");
      } catch (error) {
        alert(error.message);
      }
    },
  },

  computed: {
    phoneError () {
      let phoneReg = /^1[3-9][0-9]{9}$/;
      if (phoneReg.test(this.phone) || this.phone == '')
        return '';
      else return '手机格式不正确';
    },
    password2Error () {
      if (this.password2 != this.password)
        return '两次密码输入不一致';
      let phoneReg = /^1[3-9][0-9]{9}$/;
      if (phoneReg.test(this.password2) || this.password2 == '')
        return '';
      else return '密码格式不正确';
    }


  }
}

</script>

<style lang="less" scoped>
.register-container {
  .register {
    width: 1200px;
    height: 445px;
    border: 1px solid rgb(223, 223, 223);
    margin: 0 auto;

    h3 {
      background: #ececec;
      margin: 0;
      padding: 6px 15px;
      color: #333;
      border-bottom: 1px solid #dfdfdf;
      font-size: 20.04px;
      line-height: 30.06px;

      span {
        font-size: 14px;
        float: right;

        a {
          color: #e1251b;
        }
      }
    }

    div:nth-of-type(1) {
      margin-top: 40px;
    }

    .content {
      padding-left: 390px;
      margin-bottom: 18px;
      position: relative;

      label {
        font-size: 14px;
        width: 96px;
        text-align: right;
        display: inline-block;
      }

      input {
        width: 270px;
        height: 38px;
        padding-left: 8px;
        box-sizing: border-box;
        margin-left: 5px;
        outline: none;
        border: 1px solid #999;
      }

      img {
        vertical-align: sub;
      }

      .error-msg {
        position: absolute;
        top: 100%;
        left: 495px;
        color: red;
      }
    }

    .controls {
      text-align: center;
      position: relative;

      input {
        vertical-align: middle;
      }

      .error-msg {
        position: absolute;
        top: 100%;
        left: 495px;
        color: red;
      }
    }

    .btn {
      text-align: center;
      line-height: 36px;
      margin: 17px 0 0 55px;

      button {
        outline: none;
        width: 270px;
        height: 36px;
        background: #e1251b;
        color: #fff !important;
        display: inline-block;
        font-size: 16px;
      }
    }
  }

  .copyright {
    width: 1200px;
    margin: 0 auto;
    text-align: center;
    line-height: 24px;

    ul {
      li {
        display: inline-block;
        border-right: 1px solid #e4e4e4;
        padding: 0 20px;
        margin: 15px 0;
      }
    }
  }
}
</style>