<template>
  <div class="cart">
    <h4>全部商品</h4>
    <div class="cart-main">
      <div class="cart-th">
        <div class="cart-th1">全部</div>
        <div class="cart-th2">商品</div>
        <div class="cart-th3">单价（元）</div>
        <div class="cart-th4">数量</div>
        <div class="cart-th5">小计（元）</div>
        <div class="cart-th6">操作</div>
      </div>
      <div class="cart-body">
        <ul class="cart-list"
            v-for="(cart) in cartInfoList"
            :key=cart.id>
          <li class="cart-list-con1">
            <input type="checkbox"
                   name="chk_list"
                   :checked="cart.isChecked==1"
                   @change="updateChecked(cart,$event)">
          </li>
          <li class="cart-list-con2">
            <img :src="cart.imgUrl">
            <div class="item-msg">{{cart.skuName}}</div>
          </li>
          <li class="cart-list-con4">
            <span class="price">{{cart.skuPrice || 0}}</span>
          </li>
          <li class="cart-list-con5">
            <a href="javascript:void(0)"
               class="mins"
               @click="changeNum('decrease',-1,cart)">-</a>
            <input autocomplete="off"
                   type="text"
                   minnum="1"
                   onkeyup="value=value.replace(/[^\d]/g,'')"
                   max="10000"
                   class="itxt"
                   :value="cart.skuNum"
                   @change="changeNum('change',$event.target.value*1,cart)">
            <a href="javascript:void(0)"
               class="plus"
               @click="changeNum('increase',1,cart)">+</a>
          </li>
          <li class="cart-list-con6">
            <span class="sum">{{cart.skuPrice * cart.skuNum}}</span>
          </li>
          <li class="cart-list-con7">
            <a href="#none"
               class="sindelet"
               @click="deleteCartOfShop(cart.skuId)">删除</a>
            <br>
            <a href="#none">移到收藏</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="cart-tool">
      <div class="select-all">
        <input class="chooseAll"
               type="checkbox"
               :checked="isAllCheck&&cartInfoList.length>0"
               @click="checkedAllCart">
        <span>全选</span>
      </div>
      <div class="option">
        <a @click="deleteAllCheckedCart">删除选中的商品</a>
        <a href="#none">移到我的关注</a>
        <a href="#none">清除下柜商品</a>
      </div>
      <div class="money-box">
        <div class="chosed">已选择
          <span>0</span>件商品
        </div>
        <div class="sumprice">
          <em>总价（不含运费） ：</em>
          <i class="summoney">{{totalPrice}}</i>
        </div>
        <div class="sumbtn">
          <router-link class="sum-btn"
                       to="/trade">结算</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import _ from 'lodash';
export default {
  name: 'RouterShopCart',
  mounted () {
    this.getData();
  },
  computed: {
    // 购物车相关信息
    ...mapGetters(['cartList']),

    // 购物车内的商品信息
    cartInfoList () {
      return this.cartList.cartInfoList || [];
    },

    // 购物车内总价
    totalPrice () {
      let sum = 0;
      this.cartInfoList.forEach(element => {
        sum = sum + element.skuPrice * element.skuNum;
      });
      return sum;
    },

    // 判断底部全选复选框是否要选中（购物车内所有商品都是选中状态，该复选框自动设置被选中）
    isAllCheck () {
      return this.cartInfoList.every(item => item.isChecked == 1)
      // 数组的every函数，数组内的每一项都要判断括号内的语句的真假，全为真，该every函数返回真；
    },
  },


  methods: {
    // 获取个人购物车数据
    getData () {
      this.$store.dispatch('getCartList');
    },

    // 1、获取修改后的产品数量与原有数量的差值
    // 2、发送给服务器修改购物车数量的请求，如果服务器获取数据成功，则再次向服务器发送请求个人购物车的数据，以刷新页面
    // 3、失败者提示失败
    // 给该函数加入节流操作
    changeNum: _.throttle(async function (type, disNum, cart) {
      // 1、
      if (type == 'increase')
        disNum = 1;
      else if (type == 'decrease')
        disNum = cart.skuNum > 1 ? -1 : 0;   // 根据当前购物车内产品的数量，判断能否向服务器发送-1；大于1则发送-1；小于等于1则发送0；
      else if (type == 'change') {
        if (isNaN(disNum) || disNum < 1) {  // 检查用户输入进来的文本，如果带有非数字字符或是负数，则不改变数量
          disNum = 0;
        } else {
          disNum = parseInt(disNum) - cart.skuNum;
        }
      }
      // 2、
      try {
        await this.$store.dispatch('addOrUpdateCart', { skuId: cart.skuId, skuNum: disNum });
        this.getData();
      }
      catch (error) {
        // 3、失败-- 提示用户失败
        alert(error.message);
      }
    }, 1100),

    // async changeNum (type, disNum, cart) {
    //   // 1、
    //   if (type == 'increase')
    //     disNum = 1;
    //   else if (type == 'decrease')
    //     disNum = cart.skuNum > 1 ? -1 : 0;   // 根据当前购物车内产品的数量，判断能否向服务器发送-1；大于1则发送-1；小于等于1则发送0；
    //   else if (type == 'change') {
    //     if (isNaN(disNum) || disNum < 1) {  // 检查用户输入进来的文本，如果带有非数字字符或是负数，则不改变数量
    //       disNum = 0;
    //     } else {
    //       disNum = parseInt(disNum) - cart.skuNum;
    //     }
    //   }
    //   // 2、
    //   try {
    //     await this.$store.dispatch('addOrUpdateCart', { skuId: cart.skuId, skuNum: disNum });
    //     this.getData();
    //   }
    //   catch (error) {
    //     // 3、失败-- 提示用户失败
    //     alert(error.message);
    //   }
    // },


    // 删除购物车商品
    async deleteCartOfShop (id) {
      try {
        await this.$store.dispatch('deleteCart', id);
        this.getData();
      }
      catch (error) {
        // 3、失败-- 提示用户失败
        alert(error.message);
      }
    },

    // 更改购物车商品的选中状态
    async updateChecked (cart, e) {
      try {
        // 带给服务器的第二个参数是string类型的0/1，不是true/false，所以需要预先改变数据类型
        let checked = e.target.checked ? '1' : '0';
        await this.$store.dispatch('updateCartChecked', { skuId: cart.skuId, isChecked: checked });
        this.getData();
      }
      catch (error) {
        // 3、失败-- 提示用户失败
        alert(error.message);
      }
    },

    // 删除全部选中的商品
    async deleteAllCheckedCart () {
      try {
        await this.$store.dispatch('deleteAllCheckedCart');
        this.getData();
      }
      catch (error) {
        // 3、失败-- 提示用户失败
        alert(error.message);
      }
    },

    // 全选所有商品
    async checkedAllCart (e) {
      try {
        let isChecked = e.target.checked ? "1" : "0";
        await this.$store.dispatch('checkedAllCart', isChecked);
        this.getData();
      }
      catch (error) {
        // 3、失败-- 提示用户失败
        alert(error.message);
      }
    }
  },
}
</script>

