<script>
import QRCode from 'qrcode' // 引入qrcode插件，生成二维码图片的插件
export default {
  name: 'RouterPay',
  data () {
    return {
      payInfo: {},  // 订单支付信息
      timer: null, // 查询支付状态的定时器
      code: '',      // 查询支付状态时，后台返回的状态码
    }
  },
  computed: {
    orderId () {
      return this.$route.query.orderId;   // 获取trade结算组件，路由跳转进来携带的参数orderId
    },

  },


  mounted () {
    // mounted调用getPayInfo，向服务器发送请求，获取订单支付数据
    this.getPayInfo();
  },

  methods: {
    // 向服务器发送请求，获取订单支付数据
    async getPayInfo () {
      // 1、发送请求
      let result = await this.$API.reqGetPayInfo(this.orderId);
      // 2、获取服务器返回结果并进行处理
      if (result.code == '200') {
        // 2.1、请求成功，则获取服务器传回的支付信息数据，存储到组件当中
        this.payInfo = result.data;
      } else {
        // 2.2、请求失败则打印失败原因
        console.log(result.message);
      }
    },

    // 立即支付，弹出二维码弹窗；使用element-ui组件库及qrcode插件；
    async open () {
      let imgUrl = await QRCode.toDataURL(this.payInfo.codeUrl);
      // QRCode.toDataURL，将二维码字符串转变为图片的方法，不写await的话其返回结果是一个promise，
      // 所以写成异步函数，等待其成功后的返回结果就是生成的图片地址
      this.$alert(`<img src=${imgUrl}>`, '请微信支付', {
        dangerouslyUseHTMLString: true,
        center: true,                     // 居中布局
        showCancelButton: true,           // 显示取消按钮
        cancelButtonText: '支付遇到问题', // 取消按钮文本
        confirmButtonText: '已完成支付',  // 确定按钮文本
        showClose: false,                 // 显示弹窗右上角关闭图标
        beforeClose: (type, instance, done) => {
          //type：区分取消|确定按钮
          //instance：当前组件实例
          //done：关闭弹出框的方法

          if (type == 'cancel') {
            // 点击支付有问题：弹出提醒消息，清除定时器并将this.timer置空，关闭弹窗
            alert('请联系管理人员');
            clearInterval(this.timer);
            this.timer = null;
            done();
          }
          else {
            // 点击支付成功：确认是否真的已支付成功，清除定时器并将this.timer置空，关闭弹窗，并跳到下一路由
            // 这里，为了开发人员方便，注释掉判断条件，使不支付时点击已完成支付也能正常跳转到下一页面
            // if (this.code == 200) {
            clearInterval(this.timer);
            this.timer = null;
            this.$msgbox.close();
            this.$router.push('/paysuccess');
            // }
          }
        }
      });

      // 之后，定时向服务器发送查询支付状态的请求
      // 如果还没有定时器则开启一个定时器
      if (!this.timer) {
        this.timer = setInterval(async () => {
          // 定时向服务器发送查询支付状态的请求
          let result = await this.$API.reqPayStatus(this.orderId);
          console.log(result);
          // 如果已支付成功
          if (result.code == 200) {
            // 清除定时器并将this.timer置空
            clearInterval(this.timer);
            this.timer = null;
            // 保存返回的支付状态码
            this.code = result.code;
            // 关闭弹窗，并跳到下一路由
            this.$msgbox.close();
            this.$router.push('/paysuccess');
          }
        }, 2000);
      }
    }
  },
}
</script>

