<template>
  <header class="header">
    <!-- 头部的第一行 -->
    <div class="top">
      <div class="container">
        <div class="loginList">
          <p>QG商城欢迎您！</p>
          <p v-if="!userName">
            <span>请</span>
            <!-- 声明式路由导航 -->
            <router-link to="/login">登录</router-link>
            <router-link to="/register"
                         class="register">免费注册</router-link>
          </p>
          <p v-else>
            <a>{{userName}}</a>
            <a class="register"
               @click="logout">退出登录</a>
          </p>
        </div>
        <div class="typeList">
          <router-link to="/center">我的订单</router-link>
          <router-link to="/shopcart">我的购物车</router-link>
          <a href="###">我的QG商城</a>
          <a href="###">QG商城会员</a>
          <a href="###">企业采购</a>
          <a href="###">关注QG商城</a>
          <a href="###">合作招商</a>
          <a href="###">商家后台</a>
        </div>
      </div>
    </div>
    <!--头部第二行 搜索区域-->
    <div class="bottom">
      <h1 class="logoArea">
        <router-link class="logo"
                     to="/home">
          <img src="./images/logo.png"
               alt="" />
        </router-link>
      </h1>
      <div class="searchArea">
        <form action="###"
              class="searchForm">
          <input type="text"
                 id="autocomplete"
                 class="input-error input-xxlarge"
                 v-model="keyword" />
          <button class="sui-btn btn-xlarge btn-danger"
                  type="button"
                  @click="goSearch">
            搜索
          </button>
        </form>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  name: 'HeaderA',
  components: {
  },
  data () {
    return {
      keyword: '',
    }
  },
  methods: {
    goSearch () {					// 搜索按钮的回调函数，向search路由进行跳转并传递参数
      let location = {
        name: 'RouterSearch', params: {
          keyword: this.keyword || undefined,
        }
      };
      if (this.$route.query) {
        location.query = this.$route.query;
      }
      this.$router.push(location);
    },

    // 退出登录
    async logout () {
      try {
        await this.$store.dispatch("userLogout");
        //注册成功进行路由的跳转
        this.$router.push("/login");
      } catch (error) {
        alert(error.message);
      }
    },

  },

  mounted () {
    // 绑定自定义事件，并设置回调函数（search模块取消搜索关键字的面包屑导航时，搜索框也要清空）
    this.$bus.$on('clearSearchInput', () =>
      this.keyword = '')
  },
  computed: {
    // 用户名信息
    userName () {
      return this.$store.state.user.userInfo.name;
    },
  }
}
</script>

<style scoped lang='less'>
.header {
  & > .top {
    background-color: #eaeaea;
    height: 30px;
    line-height: 30px;

    .container {
      width: 1200px;
      margin: 0 auto;
      overflow: hidden;

      .loginList {
        float: left;

        p {
          float: left;
          margin-right: 10px;

          .register {
            border-left: 1px solid #b3aeae;
            padding: 0 5px;
            margin-left: 5px;
          }
        }
      }

      .typeList {
        float: right;

        a {
          padding: 0 10px;

          & + a {
            border-left: 1px solid #b3aeae;
          }
        }
      }
    }
  }

  & > .bottom {
    width: 1200px;
    margin: 0 auto;
    overflow: hidden;

    .logoArea {
      float: left;

      .logo {
        img {
          width: 175px;
          margin: 25px 45px;
        }
      }
    }

    .searchArea {
      float: right;
      margin-top: 35px;

      .searchForm {
        overflow: hidden;

        input {
          box-sizing: border-box;
          width: 490px;
          height: 32px;
          padding: 0px 4px;
          border: 2px solid #ea4a36;
          float: left;

          &:focus {
            outline: none;
          }
        }

        button {
          height: 32px;
          width: 68px;
          background-color: #ea4a36;
          border: none;
          color: #fff;
          float: left;
          cursor: pointer;

          &:focus {
            outline: none;
          }
        }
      }
    }
  }
}
</style>
